"use client";

import { useMemo, useState } from "react";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/app/components/ui/card";
import { Button } from "@/app/components/ui/button";
import { Input } from "@/app/components/ui/input";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/app/components/ui/table";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/app/components/ui/dialog";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/app/components/ui/alert-dialog";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/app/components/ui/select";
import { Label } from "@/app/components/ui/label";
import { toast } from "sonner";
import {
  useInstallmentPlans,
  InstallmentPlan,
  InstallmentPlanInput,
  InstallmentInterval,
  InstallmentStatus,
} from "@/app/context/installment-plan-context";

const defaultFormState = {
  id: "",
  name: "",
  amount: "",
  installments: "",
  interval: "monthly",
  startDate: "",
  status: "Active",
};

const formatDate = (dateString: string) => {
  if (!dateString) return "-";
  const date = new Date(dateString);
  if (Number.isNaN(date.getTime())) return dateString;
  return new Intl.DateTimeFormat("en-US", {
    month: "short",
    day: "numeric",
    year: "numeric",
  }).format(date);
};

export default function InstallmentPlanPage() {
  const { plans, addPlan, updatePlan, deletePlan } = useInstallmentPlans();
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [formState, setFormState] = useState(defaultFormState);
  const [editingPlanId, setEditingPlanId] = useState<string | null>(null);

  const dialogTitle = editingPlanId ? "Edit Installment Plan" : "Add Installment Plan";

  const selectedPlan = useMemo(
    () => plans.find((plan) => plan.id === editingPlanId),
    [plans, editingPlanId]
  );

  const openAddDialog = () => {
    setFormState({
      ...defaultFormState,
      interval: "monthly",
      status: "Active",
    });
    setEditingPlanId(null);
    setIsDialogOpen(true);
  };

  const openEditDialog = (plan: InstallmentPlan) => {
    setFormState({
      id: plan.id,
      name: plan.name,
      amount: plan.amount.toString(),
      installments: plan.installments.toString(),
      interval: plan.interval,
      startDate: plan.startDate,
      status: plan.status,
    });
    setEditingPlanId(plan.id);
    setIsDialogOpen(true);
  };

  const handleDialogClose = () => {
    setIsDialogOpen(false);
    setFormState(defaultFormState);
    setEditingPlanId(null);
  };

  const handleInputChange = (
    field: keyof typeof formState,
    value: string
  ) => {
    setFormState((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    const amount = Number(formState.amount);
    const installments = Number(formState.installments);

    if (!amount || !installments) {
      toast.error("Please enter valid amount and installments.");
      return;
    }

    const payload: InstallmentPlanInput = {
      name: formState.name,
      amount,
      installments,
      interval: formState.interval as InstallmentInterval,
      startDate: formState.startDate,
      status: formState.status as InstallmentStatus,
    };

    if (editingPlanId && selectedPlan) {
      updatePlan(editingPlanId, payload);
      toast.success("Plan updated successfully.");
    } else {
      addPlan(payload);
      toast.success("Plan added successfully.");
    }

    handleDialogClose();
  };

  const handleDeletePlan = (id: string) => {
    deletePlan(id);
    toast.success("Plan deleted successfully.");
  };

  return (
    <div className="p-4 md:p-6 lg:p-8 space-y-6">
      <div className="flex flex-col gap-3 md:flex-row md:items-center md:justify-between">
        <div>
          <h2 className="text-3xl font-bold tracking-tight">Installment Plan</h2>
          <p className="text-muted-foreground">
            Create and manage payment plans for your customers.
          </p>
        </div>
        <Button onClick={openAddDialog}>Add New Plan</Button>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Plans Overview</CardTitle>
          <CardDescription>All active and inactive installment plans.</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Name</TableHead>
                  <TableHead>Amount</TableHead>
                  <TableHead>Installments</TableHead>
                  <TableHead>Interval</TableHead>
                  <TableHead>Start Date</TableHead>
                  <TableHead>End Date</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead className="text-right">Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {plans.map((plan) => (
                  <TableRow key={plan.id}>
                    <TableCell className="font-medium">{plan.name}</TableCell>
                    <TableCell>${plan.amount.toLocaleString()}</TableCell>
                    <TableCell>{plan.installments}</TableCell>
                    <TableCell className="capitalize">{plan.interval}</TableCell>
                    <TableCell>{formatDate(plan.startDate)}</TableCell>
                    <TableCell>{formatDate(plan.endDate)}</TableCell>
                    <TableCell>
                      <span
                        className={`inline-flex items-center rounded-full px-2 py-1 text-xs font-semibold ${
                          plan.status === "Active"
                            ? "bg-emerald-100 text-emerald-700"
                            : "bg-muted text-muted-foreground"
                        }`}
                      >
                        {plan.status}
                      </span>
                    </TableCell>
                    <TableCell className="text-right space-x-2">
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => openEditDialog(plan)}
                      >
                        Edit
                      </Button>
                      <AlertDialog>
                        <AlertDialogTrigger asChild>
                          <Button variant="outline" size="sm">
                            Delete
                          </Button>
                        </AlertDialogTrigger>
                        <AlertDialogContent>
                          <AlertDialogHeader>
                            <AlertDialogTitle>Delete plan?</AlertDialogTitle>
                            <AlertDialogDescription>
                              This will remove the plan permanently. You can add it again later if needed.
                            </AlertDialogDescription>
                          </AlertDialogHeader>
                          <AlertDialogFooter>
                            <AlertDialogCancel>Cancel</AlertDialogCancel>
                            <AlertDialogAction onClick={() => handleDeletePlan(plan.id)}>
                              Delete
                            </AlertDialogAction>
                          </AlertDialogFooter>
                        </AlertDialogContent>
                      </AlertDialog>
                    </TableCell>
                  </TableRow>
                ))}
                {plans.length === 0 && (
                  <TableRow>
                    <TableCell colSpan={8} className="text-center text-muted-foreground">
                      No plans found. Click &quot;Add New Plan&quot; to create one.
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>

      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <DialogContent className="max-w-lg">
          <DialogHeader>
            <DialogTitle>{dialogTitle}</DialogTitle>
            <DialogDescription>
              {editingPlanId ? "Update the plan details." : "Add a new installment plan."}
            </DialogDescription>
          </DialogHeader>
          <form className="space-y-4" onSubmit={handleSubmit}>
            <div className="space-y-2">
              <Label htmlFor="planName">Plan Name</Label>
              <Input
                id="planName"
                value={formState.name}
                onChange={(event) => handleInputChange("name", event.target.value)}
                placeholder="Enter plan name"
                required
              />
            </div>

            <div className="grid gap-4 sm:grid-cols-2">
              <div className="space-y-2">
                <Label htmlFor="planAmount">Amount ($)</Label>
                <Input
                  id="planAmount"
                  type="number"
                  min="1"
                  value={formState.amount}
                  onChange={(event) => handleInputChange("amount", event.target.value)}
                  placeholder="1200"
                  required
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="planInstallments">Installments</Label>
                <Input
                  id="planInstallments"
                  type="number"
                  min="1"
                  value={formState.installments}
                  onChange={(event) => handleInputChange("installments", event.target.value)}
                  placeholder="12"
                  required
                />
              </div>
            </div>

            <div className="grid gap-4 sm:grid-cols-2">
              <div className="space-y-2">
                <Label>Interval</Label>
                <Select
                  value={formState.interval}
                  onValueChange={(value) => handleInputChange("interval", value)}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Select interval" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="monthly">Monthly</SelectItem>
                    <SelectItem value="weekly">Weekly</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label>Status</Label>
                <Select
                  value={formState.status}
                  onValueChange={(value) => handleInputChange("status", value)}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Select status" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Active">Active</SelectItem>
                    <SelectItem value="Inactive">Inactive</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="planStartDate">Start Date</Label>
              <Input
                id="planStartDate"
                type="date"
                value={formState.startDate}
                onChange={(event) => handleInputChange("startDate", event.target.value)}
                required
              />
            </div>

            <DialogFooter className="gap-2 sm:gap-0">
              <Button type="button" variant="outline" onClick={handleDialogClose}>
                Cancel
              </Button>
              <Button type="submit">{editingPlanId ? "Update Plan" : "Add Plan"}</Button>
            </DialogFooter>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
}

